html {
  height: 100%;
}
body {
  overflow-x: hidden;
  height: 100%;
}
#root {
  height: 100%;
}
.img {
  /* height: fit-content; */
  /* width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; */
  position: relative;
}
.banner {
  position: relative;
  text-align: center;
  color: white;
  font-family: 'Roboto Slab', serif;

}
.banner-text {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -25%);
}
.title {
  font-size: 60px;
  width: 100%;
  text-shadow: 1px 1px black;
}
.subtitle {
  font-size: 55px;
  width: 100%;
  text-shadow: 1px 1px black;
}
.home-text {
  margin: 20px auto;
}
.home-text h1 {
  margin-bottom: 20px;
}
.about-title {
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -35%);
  font-size: 60px;
  width: 100%;
  text-shadow: 1px 1px black;
}
.about-subtitle {
  position: absolute;
  text-shadow: 1px 1px black;
  font-size: 60px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30%;
  font-size: 16px;
  display: flex;
  justify-content: space-evenly;
}
.about {
  font-size: 18px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  line-height: 1.6;
  display: flex;
  width: 100%;
  padding: 20px;
  flex-direction: column;
  width: 100%;
}
.about-text {
  margin: 0 auto;
}
.certificate {
  width: 32%;
  margin-right: 10px;
}
.certs {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.donate-btn {
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -60%);
  font-size: 32px;
  border-radius: 5px;
  background-color: navy;
  border: none;
  padding: 10px;
}
.donate-btn:hover{
  border: 1px solid black;
}
.contact {
  background-color: #48734d;
  height: 100%;
  width: 100vw;
  padding-top: 5%;
  text-align: center;
  color: white;
}
.contact-title {
  font-size: 32px;
  padding-top: 30px;
  font-family: 'Roboto Slab', serif;
  text-shadow: 1px 1px black;
}
#contact-form {
  width: 50%;
  margin: 0 auto;
}
.donate, .home-ctr, .about-ctr {
  position: absolute
}
.donate {
  width: 100%;
}
.donate-img {
  width: 100%;
  height: 100vh;
}

/* contact form modal */
/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  color: black;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 25%; /* Could be more or less, depending on screen size */
}

/* The Close Button */
.close {
  color: #aaa;
  align-self: flex-end;
  width: 28px;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.green{
  width: 20px;
  height: 20px;
  color: green;
}

.red{
  color: red;
}

@media screen and (max-width: 480px) {
  .title, .subtitle {
    font-size: 32px;
  }
}